.sumBox-container{

    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    gap: 30px;
    width: 100%;
    height: 100%;
    max-width: 35vw;
    min-height: 85vh;
    background-color: white;
    border: 1px solid black;
}


.sumBox-container h1{
    text-align: center;
    font-size: 1.5vw;

}

.sumBox-container img{
    width: 100%;
    height: 100%;
    max-height: 20vw;
    height: 100vh;
}

.details p{
    margin-top: 10px;
}